@font-face {
  font-family: 'FoundationSans';
  src: local('FoundationSans'), url(./assets/FoundationSans-BlackEx.otf) format('opentype');
}

body {
  margin: 0;
  font-family: 'FoundationSans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: white;
}

a:visited {
  color: white;
}