#background-video {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    object-fit: cover;
    z-index: -1;
    transform: translate(-50%, -50%);
}

.overlay {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    text-align: center;
    color: white;
    opacity: 0.8;
    align-content: center;
    height: 100%;
    justify-content: center;
}

.overlay .logo img {
    background: white;
    width: 100%;
    height: auto;
    color: black;
    opacity: 1;
}

.overlay .video-title {
    background: black;
    display: flex;
    animation: glitch 1s linear infinite;
    font-size: 10em;
}

.overlay .enter-now {
    margin-top: 50px;
    font-size: 6vw;
    border: 5px solid white;
    color: white;
    background: black;
    display: inline-block;
    padding: 10px 20px;
}

.overlay .enter-now:hover {
    margin-top: 50px;
    font-size: 10em;
    border: 5px solid black;
    color: black;
    background: white;
}





@keyframes glitch{
    2%,64%{
        transform: translate(2px,0) skew(0deg);
    }
    4%,60%{
        transform: translate(-2px,0) skew(0deg);
    }
    62%{
        transform: translate(0,0) skew(5deg);
    }
}

div:before,
div:after{
    content: attr(title);
    position: absolute;
    left: 0;
}

div:before{
    animation: glitchTop 1s linear infinite;
    clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
}

@keyframes glitchTop{
    2%,64%{
        transform: translate(2px,-2px);
    }
    4%,60%{
        transform: translate(-2px,2px);
    }
    62%{
        transform: translate(13px,-1px) skew(-13deg);
    }
}

div:after{
    animation: glitchBotom 1.5s linear infinite;
    clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
}

@keyframes glitchBotom{
    2%,64%{
        transform: translate(-2px,0);
    }
    4%,60%{
        transform: translate(-2px,0);
    }
    62%{
        transform: translate(-22px,5px) skew(21deg);
    }
}